.container-main{
  height: 100vh;
  width: 100%;
  display: grid;
  place-items: center center;
  background-color: rgb(255, 250, 178);
}

.container{
  background-color: #fff;
  height: 20em;
  width: 15em;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  box-shadow: 1px 7px 29px 0px rgba(0,0,0,0.75);
}

.screen-emoji{
  padding: 1em;
  background-color: #fcf5f5;
  border-radius: 10px;
}

.emoji-button{
  margin-top: 3em;
  border: none;
  padding: 1em;
  border-radius: 10px;
  background-color: rgb(255, 247, 178);
  box-shadow: none;
  transition: 0.3s ease-in-out;
  font-weight: 600;
}

.emoji-button:hover{
  background-color: rgb(251, 250, 197);
  cursor: pointer;
}

.emoji-button:active{
  transform: scale(0.9);
}

.emoji{
  font-size: 5em;
}